import React from "react";
import Header from "../layout/Header";
import LoginForm from "../components/forms/LoginForm";
import {FlashCard} from "../components/cards/Cards";

function LoginPage() {
    return <>
        <Header />
        <LoginForm renders = {{renderError: () => <FlashCard duration = {2500} type = "error">No se pudo autenticar</FlashCard>}}/>
    </>
}

export default LoginPage;